import { db, storageService } from "fbase";
import React, { useState } from "react";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faTrash, faPencilAlt } from "@fortawesome/free-solid-svg-icons";

export const Nweet = ({ nweetObj, isOwner }) => {
  const [editing, SetEditing] = useState(false);
  const [newNweet, SetNewNweet] = useState(nweetObj.text);
  const onDeleteClick = async () => {
    const ok = window.confirm("Are you sure you want to delete this nweet?");
    if (ok) {
      await db.doc(`nweets/${nweetObj.id}`).delete();
      if (nweetObj.downloadURL !== "") {
        await storageService.refFromURL(nweetObj.downloadURL).delete();
      }
    }
  };

  const toggleEditing = () => SetEditing((prev) => !prev);

  const onSubmit = (event) => {
    event.preventDefault();
    db.doc(`nweets/${nweetObj.id}`).update({
      text: newNweet,
    });
    SetEditing(false);
  };

  const onChange = (event) => {
    const {
      target: { value },
    } = event;
    SetNewNweet(value);
  };
  return (
    <div className="nweet">
      {editing ? (
        <>
          <form onSubmit={onSubmit} className="container nweetEdit">
            <input
              type="text"
              placeholder="Edit yout nweet"
              value={newNweet}
              onChange={onChange}
              required
              autoFocus
              className="formInput"
            />
            <input type="submit" value="Update Nweet" className="formBtn" />
          </form>
          <span onClick={toggleEditing} className="formBtn cancelBtn">
            Cancel
          </span>
        </>
      ) : (
        <>
          <h4>{nweetObj.text}</h4>
          {nweetObj.downloadURL && (
            <img src={nweetObj.downloadURL} alt="Nweet" />
          )}
          {isOwner ? (
            <div class="nweet__actions">
              <span onClick={onDeleteClick}>
                <FontAwesomeIcon icon={faTrash} />
              </span>
              <span onClick={toggleEditing}>
                <FontAwesomeIcon icon={faPencilAlt} />
              </span>
            </div>
          ) : (
            <></>
          )}
        </>
      )}
    </div>
  );
};
