import { db, storageRef } from "fbase";
import { v4 as uuidv4 } from "uuid";
import React, { useState } from "react";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  faBorderNone,
  faPlus,
  faTimes,
} from "@fortawesome/free-solid-svg-icons";

const NweetFactory = ({ userObj }) => {
  let nweetObj;
  const [nweet, setNweet] = useState("");
  const [file, SetFile] = useState("");

  const onSubmit = async (event) => {
    if (nweet === "") {
      return;
    }
    event.preventDefault();
    let downloadURL = "";
    let imgUuid = "";
    if (file !== "") {
      imgUuid = uuidv4();
      const fileRef = storageRef.child(`${userObj.uid}/${imgUuid}`);
      const response = await fileRef.putString(file, "data_url");
      downloadURL = await response.ref.getDownloadURL();
    }
    nweetObj = {
      text: nweet,
      createdAt: Date.now(),
      creatorId: userObj.uid,
      downloadURL,
    };
    await db.collection("nweets").add(nweetObj);
    setNweet("");
    SetFile("");
  };

  const onChange = (event) => {
    const {
      target: { value },
    } = event;
    setNweet(value);
  };

  const onFileChange = (event) => {
    const {
      target: { files },
    } = event;
    const theFile = files[0];
    const reader = new FileReader();
    reader.onloadend = (finishedEvent) => {
      const {
        currentTarget: { result },
      } = finishedEvent;
      SetFile(result);
    };
    reader.readAsDataURL(theFile);
  };

  const onClearFileClick = () => {
    SetFile("");
  };
  return (
    <form onSubmit={onSubmit} className="factoryForm">
      <div className="factoryInput__container">
        <input
          className="factoryInput__input"
          value={nweet}
          onChange={onChange}
          type="text"
          placeholder="What's on your mind?"
          maxLength={120}
        />
        <input type="submit" value="&rarr;" className="factoryInput__arrow" />
      </div>
      <label for="attach-file" className="factoryInput__label">
        <span>Add photos</span>
        <FontAwesomeIcon icon={faPlus} />
      </label>
      <input
        id="attach-file"
        type="file"
        accept="image/*"
        onChange={onFileChange}
        style={{
          opacity: 0,
        }}
        hidden
      />
      {file && (
        <div className="factoryForm__attachment">
          <img
            alt="Nweet"
            src={file}
            style={{
              backgroundImage: file,
            }}
          />
          <div id="modal-1" class="modal">
            <div class="modal-inner">
              <a class="js-close-modal">&times;</a>
              <div class="modal-content">
                <h3>Modal Title</h3>
                <p>This is my modal content</p>
              </div>
            </div>
          </div>

          <div class="table">
            <div class="table-cell">
              <button class="js-modal" data-modal="modal-1">
                Trigger Modal
              </button>
            </div>
          </div>
          <div className="factoryForm__clear" onClick={onClearFileClick}>
            <span>Remove</span>
            <FontAwesomeIcon icon={faTimes} />
          </div>
        </div>
      )}
    </form>
  );
};

export default NweetFactory;
